import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { useParams } from 'react-router-dom'
import TeamDB from '@model/team.model'
import dayjs from 'dayjs'

const Team = () => {
  const [teams, setTeams] = useState([])
  const params = useParams()
  const navigate = useNavigate()

  const onSignup = id => navigate(`/${id}/signup`)

  useEffect(() => {
    const userId = localStorage.getItem('user')
    const teams = TeamDB.getByExtraId(params.id).filter(x => x.user.userId !== userId)

    setTeams(teams)
  }, [])

  return (
    <div className="row my-3">
      {(teams.length !== 0 &&
        teams.map(item => (
          <div className="col-4" key={item.id}>
            <div className="card">
              <div className="card-header bg-primary text-white">
                <h5 className="card-title">{item.title}</h5>
              </div>
              <div className="card-body">
                <h6>出團時間：</h6>
                <div className="text-muted mb-2">
                  {dayjs(item.time_start).format('YYYY/MM/DD')}
                  {item.time_end !== item.time_start && ~dayjs(item.time_end).format('YYYY/MM/DD')}
                </div>
                <h6>主辦人</h6>
                <p className="text-muted mb-2">{item.user.name}</p>
                <h6>地點：</h6>
                <div className="card-subtitle mb-2 text-muted">{item.location}</div>
                <h6>揪團描述：</h6>
                <p className="text-muted mb-2">{item.description}</p>
                <h6>主辦分已有人數</h6>
                <p>
                  男性：{item.founders.male} / 女性：{item.founders.female} / 小孩：{item.founders.child}
                </p>
                <h6>揪團人員需求：</h6>
                {!item.memberNonLimit && (
                  <p className="text-muted mb-2">
                    男性：{item.members.male} / 女性：{item.members.female} / 小孩：{item.members.child} , 總計{' '}
                    {item.memberAmount} 人
                  </p>
                )}
                {item.memberNonLimit && <p className="text-muted mb-2">不拘</p>}

                <button type="button" className="btn btn-success" onClick={() => onSignup(item.id)}>
                  我要報名
                </button>
                <button type="button" className="btn btn-danger ms-3" onClick={() => TeamDB.clearTeam(item.id)}>
                  清除資料
                </button>
              </div>
            </div>
          </div>
        ))) || (
        <div className="alert alert-danger" role="alert">
          目前無符合您的揪團資訊
        </div>
      )}
    </div>
  )
}

export default Team
