import * as yup from 'yup'

export const TeamFromSchema = yup
  .object({
    title: yup
      .string()
      .min(1, '請輸入標題')
      .required(),
    description: yup.string().min(1, '請輸入活動描述'),
    founder: yup.string(),
    time_start: yup.date(),
    time_end: yup.date(),
    members: yup.object({
      male: yup.number(),
      female: yup.number(),
      child: yup.number()
    }),
    founders: yup.object({
      male: yup.number(),
      female: yup.number(),
      child: yup.number()
    }),
    memberAmount: yup.number().min(1, '出團人數需大於 0')
  })
  .required()

export const SignSchema = yup.object({
  members: yup.object({
    male: yup.number(),
    female: yup.number(),
    child: yup.number()
  })
})
