import React, { useEffect, useState } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { useParams, useNavigate } from 'react-router-dom'
import { SignSchema } from '@utils/validate'
import TeamDB from '@model/team.model'
import { sendMessage } from '@utils/message'

const Signup = ({ toast }) => {
  const params = useParams()
  const navigate = useNavigate()

  const [quota, setQuota] = useState({})
  const { memberAmount, quotaAmount } = quota
  const { register, handleSubmit, reset } = useForm({
    resolver: yupResolver(SignSchema),
    defaultValues: {
      members: { male: 0, female: 0, child: 0 }
    }
  })

  const onSubmit = async data => {
    try {
      const { success, message } = await TeamDB.signUpTeam({ ...data, id: params.id })
      if (!success) {
        return toast.error(message, { position: 'top-right', autoClose: 1000 })
      }
      sendMessage(data)
      refresh()
      toast.success(message, { position: 'top-right', autoClose: 1000, onClose: () => navigate(-1) })
      reset()
    } catch (err) {
      toast.error(err, { position: 'top-right', autoClose: 1000 })
      throw err
    }
  }

  const refresh = async () => {
    const { memberAmount, quotaAmount } = await TeamDB.getTeamQuota(params.id)
    setQuota({ memberAmount, quotaAmount })
  }

  useEffect(async () => {
    await refresh()
  }, [])

  return (
    <div className="container">
      {memberAmount === quotaAmount ? (
        <div className="row">
          <div className="alert alert-danger my-3" role="alert">
            此揪團已滿，無法報名囉！
          </div>
          <button className="btn btn-danger" onClick={() => navigate(-1)}>
            返回
          </button>
        </div>
      ) : (
        <>
          <h1 className="my-4 text-center">我要報名</h1>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="row">
              <div className="col-12 mb-3">
                活動參與人數：
                <span className="text-success">
                  {quota.quotaAmount} / {quota.memberAmount}
                </span>
                人
              </div>
              <div className="col-4 mb-3">
                <label htmlFor="male" className="form-label">
                  男性
                </label>
                <input
                  type="number"
                  min="0"
                  className="form-control"
                  id="male"
                  {...register('members.male', { required: true, min: 0 })}
                />
              </div>
              <div className="col-4 mb-3">
                <label htmlFor="female" className="form-label">
                  女性
                </label>
                <input
                  type="number"
                  min="0"
                  className="form-control"
                  id="female"
                  defaultValue="0"
                  {...register('members.female', { required: true, min: 0 })}
                />
              </div>
              <div className="col-4 mb-3">
                <label htmlFor="child" className="form-label">
                  小孩
                </label>
                <input
                  type="number"
                  min="0"
                  className="form-control"
                  id="child"
                  defaultValue="0"
                  {...register('members.child', { required: true, min: 0 })}
                />
              </div>
              <div className="d-flex">
                <button type="submit" className="flex-fill btn btn-primary me-3">
                  送出
                </button>
                <button type="button" className="flex-fill btn btn-secondary" onClick={() => reset()}>
                  重置
                </button>
              </div>
            </div>
          </form>
        </>
      )}
    </div>
  )
}

export default Signup
