import liff from '@line/liff'
import dayjs from 'dayjs'
import { toast } from 'react-toastify'

export const sendMessage = async data => {
  await liff.ready
  const { location, memberAmount, time_start, time_end } = data
  const contextType = (liff.getContext() || {}).type
  const isInChat = ~['utou', 'room', 'group'].indexOf(contextType)
  if (!isInChat) return toast.error('您尚未在 Line 軟體內', { position: 'top-right', autoClose: 3000 })

  try {
    liff
      .sendMessages([
        {
          type: 'text',
          text: `各位揪友好！我是${data.user.displayName}。將會舉辦從${dayjs(time_start).format(
            'YYYY/MM/DD'
          )} 至 ${dayjs(time_end).format(
            'YYYY/MM/DD'
          )} 活動，地點在 ${location}，人數需要 ${memberAmount}人，若有興趣，歡迎報名！`
        }
      ])
      .then(() => toast.success('訊息已發送', { position: 'top-right', autoClose: 3000 }))
  } catch (err) {
    toast.error(err, { position: 'top-right', autoClose: 3000 })
  }
}
