import React, { useState, useEffect } from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import liff from '@line/liff'

import Item from '@pages/Item'
import Team from '@pages/Team'
import Signup from '@pages/Signup'
import TeamForm from '@pages/TeamForm'
import TeamDB from '@model/team.model'

import { getAttractions } from '@utils/fetchAttractions'
import { sendMessage } from '@utils/message'
import 'react-toastify/dist/ReactToastify.css'

function App() {
  const [isLogin] = useState(true)
  const [user, setUser] = useState({})
  const [team, setTeam] = useState({})

  useEffect(async () => {
    await liff.init({ liffId: process.env.REACT_APP_LIFFID, withLoginOnExternalBrowser: true })
    // if (!liff.isLoggedIn()) liff.login()
    // setUser(await liff.getProfile())
    setUser({ displayName: 'Chuchu', userId: '002' })
    localStorage.setItem('user', user.userId)
  }, [user.userId])

  useEffect(() => {
    setTeam(TeamDB.getAmount(user.userId))
  }, [])

  return (
    <div className="container">
      <Router basename={process.env.NODE_ENV === 'development' ? '' : '/TravelFunSite'}>
        <Routes>
          <Route
            exact
            path="/"
            element={
              isLogin &&
              getAttractions(5).map(item => <Item key={item.Id} data={item} amount={team[item.Id] || false} />)
            }
          />
          <Route path="/:id/registration" element={<TeamForm user={user} toast={toast} />} />
          <Route path="/:id/teams" element={<Team />} />
          <Route path="/:id/signup" element={<Signup toast={toast} />} />
        </Routes>
      </Router>
      <ToastContainer />
    </div>
  )
}
export default App
