import React from 'react'
import defaultImg from '@/images/default.jpeg'
import { useNavigate } from 'react-router-dom'

const Item = ({ data, amount }) => {
  const navigate = useNavigate()
  const { Id, Name, Picture1, Description } = data
  const addTeam = id => navigate(`${id}/registration`)
  const visitTeam = id => navigate(`${id}/teams`)

  return (
    <div className="card my-3">
      <div className="row g-0">
        <div className="col-md-4">
          <img src={Picture1 || defaultImg} className="img-fluid rounded-start" alt={Name} />
        </div>
        <div className="col-md-8">
          <div className="card-body">
            <h5 className="card-title">{Name}</h5>
            <div className="card-text">{Description}</div>
            <div className="d-flex justify-content-end mt-3">
              <div className="btn-group">
                {amount && (
                  <button type="button" className="btn btn-success" onClick={() => visitTeam(Id)}>
                    我想報名
                  </button>
                )}
                <button type="button" className="btn btn-outline-secondary" onClick={() => addTeam(Id)}>
                  我要揪團
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Item
