import React, { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { useParams, useNavigate } from 'react-router-dom'
import { get } from '@model/attractions.model'
import { TeamFromSchema } from '@utils/validate'
import { sendMessage } from '@utils/message'
import TeamDB from '@/model/team.model'
import DatePicker from 'react-datepicker'

import 'react-datepicker/dist/react-datepicker.css'

export default function TeamForm({ user, toast }) {
  const params = useParams()
  const navigate = useNavigate()

  const [currentLocation, setCurrentLocation] = useState({
    location: ''
  })
  const [time_start, setTimeStart] = useState(new Date())
  const [time_end, setTimeEnd] = useState(new Date())
  const [memberNonLimit, setMemberNonLimit] = useState(false)

  const {
    register,
    handleSubmit,
    formState: { errors }
  } = useForm({
    resolver: yupResolver(TeamFromSchema),
    defaultValues: {
      title: 'Test001',
      description: 'Testing',
      extraId: params.id,
      memberAmount: 1,
      members: { male: 0, female: 0, child: 0 },
      founders: { male: 0, female: 0, child: 0 }
    }
  })
  useEffect(async () => {
    try {
      const { Name } = await get(params.id)
      setCurrentLocation({ location: Name })
    } catch (err) {
      toast.error(err, { position: 'top-right', autoClose: 1000 })
      throw err
    }
  }, [])

  const onSubmit = async data => {
    if (!validateJoinMember(data)) {
      toast.error('揪團人數與設定男女人數不相同', { position: 'top-right', autoClose: 1000 })
      return
    }
    data['location'] = currentLocation.location
    data['time_start'] = time_start
    data['time_end'] = time_end
    data['user'] = user
    data['status'] = data['memberAmount'] === 1 ? 'START' : 'PENDING'
    data['memberNonLimit'] = memberNonLimit
    await TeamDB.createOrUpdate(data)
    sendMessage(data)
    navigate('/')
  }

  const cancel = () => navigate('/')

  const validateJoinMember = data => {
    let { members, founders, memberAmount } = data
    let joinTotal = 0
    for (let founder in founders) {
      joinTotal += founders[founder]
    }
    if (!memberNonLimit) {
      for (let member in members) {
        joinTotal += members[member]
      }
      return joinTotal === memberAmount
    }
    return true
  }

  return (
    <div className="container">
      <h1 className="my-4 text-center">團隊申請</h1>
      <p className="text-muted text-center">親愛的會員您好，本申請表格僅提供「景點/活動」揪團作業。</p>
      <form onSubmit={handleSubmit(onSubmit)}>
        <input type="hidden" name="extraId" />
        <div className="row">
          <div className="col-12 mb-3">
            <input
              type="text"
              className={`form-control ${errors.title && 'is-invalid'}`}
              id="title"
              placeholder="請輸入您的標題"
              {...register('title', {
                required: true,
                maxLength: 50
              })}
            />
            {errors.title && <div className="invalid-feedback">{errors.title.message}</div>}
          </div>
          <div className="col-12 mb-3">
            <textarea
              className={`form-control ${errors.description && 'is-invalid'}`}
              id="description"
              rows="5"
              placeholder="請輸入出團的描述，如揪團限制、交通方式以及其他注意事項"
              {...register('description', { required: true })}
            />
            {errors.description && <div className="invalid-feedback">{errors.description.message}</div>}
          </div>
          <div className="col-6 mb-3">
            <label htmlFor="founder" className="form-label">
              主辦人
            </label>
            <input
              id="founder"
              type="text"
              className="form-control"
              name="founder"
              value={user.displayName || ''}
              disabled
            />
          </div>
          <div className="col-6 mb-3">
            <label htmlFor="location" className="form-label">
              出遊地點
            </label>
            <input
              type="text"
              min="0"
              className="form-control"
              id="location"
              value={currentLocation.location}
              disabled
            />
          </div>
          <div className="col-6 mb-3">
            <label htmlFor="time_start" className="form-label">
              出發時間
            </label>
            <DatePicker
              id="time_start"
              dateFormat="yyyy-MM-dd"
              selected={time_start}
              onChange={date => setTimeStart(date)}
              className="form-control"
            />
          </div>
          <div className="col-6 mb-3">
            <label htmlFor="time_end" className="form-label">
              結束時間
            </label>
            <DatePicker
              id="time_end"
              dateFormat="yyyy-MM-dd"
              selected={time_end}
              onChange={date => setTimeEnd(date)}
              minDate={time_start}
              className="form-control"
            />
          </div>
          <div className="col-12 mb-3">
            <label htmlFor="memberAmount" className="form-label">
              揪團人數
            </label>
            <input
              type="number"
              min="1"
              className={`form-control ${errors.memberAmount && 'is-invalid'}`}
              id="memberAmount"
              defaultValue="1"
              {...register('memberAmount', { required: true, min: 1 })}
            />
            {errors.memberAmount && <div className="invalid-feedback">{errors.memberAmount.message}</div>}
          </div>
          <div className="col-4 mb-3">
            <label htmlFor="male" className="form-label">
              主辦方男性
            </label>
            <input
              type="number"
              min="0"
              className="form-control"
              id="male"
              {...register('founders.male', { required: true, min: 0 })}
            />
          </div>
          <div className="col-4 mb-3">
            <label htmlFor="female" className="form-label">
              主辦方女性
            </label>
            <input
              type="number"
              min="0"
              className="form-control"
              id="female"
              defaultValue="0"
              {...register('founders.female', { required: true, min: 0 })}
            />
          </div>
          <div className="col-4 mb-3">
            <label htmlFor="child" className="form-label">
              主辦方小孩
            </label>
            <input
              type="number"
              min="0"
              className="form-control"
              id="child"
              defaultValue="0"
              {...register('founders.child', { required: true, min: 0 })}
            />
          </div>
          <div className="col-4 mb-3">
            <label htmlFor="male" className="form-label">
              男性
            </label>
            <input
              type="number"
              min="0"
              className="form-control"
              id="male"
              disabled={memberNonLimit}
              {...register('members.male', { required: true, min: 0 })}
            />
          </div>
          <div className="col-4 mb-3">
            <label htmlFor="female" className="form-label">
              女性
            </label>
            <input
              type="number"
              min="0"
              className="form-control"
              id="female"
              defaultValue="0"
              disabled={memberNonLimit}
              {...register('members.female', { required: true, min: 0 })}
            />
          </div>
          <div className="col-4 mb-3">
            <label htmlFor="child" className="form-label">
              小孩
            </label>
            <input
              type="number"
              min="0"
              className="form-control"
              id="child"
              defaultValue="0"
              disabled={memberNonLimit}
              {...register('members.child', { required: true, min: 0 })}
            />
          </div>
          <div className="col-6 mb-3">
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                id="membersNoLimit"
                {...register('membersNoLimit')}
                onChange={() => setMemberNonLimit(!memberNonLimit)}
              />
              <label className="form-check-label" htmlFor="membersNoLimit">
                不拘
              </label>
            </div>
          </div>
        </div>
        <div className="d-flex justify-content-end">
          <button type="button" className="btn btn-outline-secondary btn-lg me-3" onClick={cancel}>
            取消
          </button>
          <button type="submit" className="btn btn-primary btn-lg">
            送出
          </button>
        </div>
      </form>
    </div>
  )
}
