import { v4 as uuid } from 'uuid'
import dayjs from 'dayjs'
import { countMemberAmount } from '@utils/member'

class TeamDB {
  constructor() {
    this.collection = 'team'
    this.data = JSON.parse(localStorage.getItem('team')) || []
  }
  getAll() {
    return this.data
  }
  getByUser(userId) {
    return this.data.filter(x => x.userId === userId)
  }

  get(id) {
    try {
      const result = this.data.filter(x => x.id === id)
      if (result.length !== 0) return result[0]
      else return null
    } catch (err) {
      console.error(`Get data ${id} failed, because ${err}`)
      throw err
    }
  }

  getByExtraId(id) {
    try {
      const result = this.data.filter(x => x.extraId === id)
      if (result.length !== 0) return result
      else return null
    } catch (err) {
      console.error(`Get data ${id} failed, because ${err}`)
      throw err
    }
  }

  updateTeam(origin, target) {
    let keys = Object.keys(origin)
    for (let key of keys) {
      origin[key] = target[key]
    }
    origin['time_update'] = dayjs().format('YYYY/MM/DD HH:mm:ss')
    origin['status'] = 'PENDING'
    return { success: true, message: 'success update team.' }
  }

  createTeam(data) {
    let temp = data
    temp['id'] = uuid()
    temp['time_create'] = dayjs().format('YYYY/MM/DD HH:mm:ss')
    this.data.push(temp)
    return { success: true, message: temp['id'] }
  }

  createOrUpdate(data) {
    const result = this.get(data.id)
    if (result) this.updateTeam(result, data)
    else this.createTeam(data)

    this.saveToStore()
  }

  getAmount(userId) {
    try {
      const items = this.data
      let temp = {}
      items.forEach(item => {
        if (item.user.userId !== userId) {
          if (typeof temp[item['extraId']] == 'undefined') temp[item['extraId']] = 1
          else temp[item['extraId']]++
        }
      })
      return temp
    } catch (err) {
      console.error(err)
      throw err
    }
  }

  getTeamQuota(id) {
    try {
      const target = this.get(id)
      let quota = 0
      let { members, founders, memberAmount } = target
      for (let founder in founders) quota += founders[founder]
      for (let member in members) quota += members[member]
      return { memberAmount, quotaAmount: quota }
    } catch (err) {
      console.error(err)
      throw err
    }
  }

  // 報名
  async signUpTeam(data) {
    try {
      const { id, members } = data
      const target = this.get(id)
      const { memberAmount, quotaAmount } = this.getTeamQuota(id)
      let neededQuota = countMemberAmount(members)
      if (!members) return { success: false, message: 'Members is empty.' }

      if (neededQuota > memberAmount - quotaAmount) return { success: false, message: '人數超過揪團人數' }

      for (let member in members) {
        target['members'][member] += members[member]
      }

      await this.createOrUpdate(target)
      return { success: true, message: 'Success save in storage.' }
    } catch (err) {
      console.log(err)
    }
  }

  clearTeam(id) {
    const target = this.get(id)
    const { members } = target
    for (let member in members) {
      members[member] = 0
    }
    this.createOrUpdate(target)
  }

  saveToStore() {
    try {
      localStorage.removeItem('team')
      localStorage.setItem('team', JSON.stringify(this.data))
    } catch (err) {
      throw err
    }
  }
}

export default new TeamDB()
